        p De acuerdo con Lucio (1978) y la UNESCO (2016), hoy en da, los Instructores del siglo XXI, no deben mirar a los aprendices como una masa, sino que cada uno tiene sus necesidades particulares, aunque el Instructor los puede agrupar, siempre va a encontrar individualidades. Se debe recordar que el instructor actualmente asume el papel de mediador del conocimiento y que carece de tcnicas y procedimientos infalibles, que en cualquier actividad de evaluacin que se plantee, no necesariamente esta es infalible; el hecho de que los aprendices no logren el porcentaje establecido adecuado, quiere decir que hay una falla en el diseo de la tcnica o del procedimiento;  en algunos casos requiere de la participacin de diferentes personas, as como de  una gran variedad de tcnicas para recoger informacin, para lograr no la recompensa o el castigo sino  investigar cmo mejorar el producto y el proceso de aprendizaje. 
      .col-lg-5(data-aos='fade-left'): img(src='@/assets/curso/5.svg', alt='Texto que describa la imagen').mb-3
    p.mb-5 En ese sentido, la evaluacin debe contar con dos principios:
