            p Conocer los referentes de la Medicin y Evaluacin de Aprendizajes es una respuesta a la necesidad concreta detectada en nuestro pas, principalmente en el mbito educativo, de formar equipos de profesionales preparados para desarrollar instrumentos de medicin vlidos, confiables, relevantes y pertinentes, y para utilizar adecuadamente los resultados de las mediciones para promover el aprendizaje de los estudiantes y evaluar la eficiencia y eficacia de las acciones pedaggicas implementadas (Manzi, 2009).
              br
