          .col-md-9.mb-4.mb-md-0
            h4 Glosario
            p <b>Aprendizaje.</b> Resultado observado en forma de cambio ms o menos permanente del comportamiento de una persona, que se produce como consecuencia de una accin sistemtica (por ejemplo de la enseanza) o simplemente de una prctica realizada por el aprendiz.
              br
              br
              |<b>Autoevaluacin.</b> Procedimiento de evaluacin segn el cual un estudiante se evala a si mismo, emitiendo juicios sobre el aprendizaje logrado.
              br
              br
              |<b>Calificacin.</b> Expresin numrica o nominal que genera normalmente un profesor, tutor o supervisor, para resumir la valoracin de los logros de aprendizaje conseguidos por el estudiante.
              br
              br
              |<b>Criterios de evaluacin.</b> Referentes que se adoptan para establecer una comparacin con el objeto evaluado. En el caso del aprendizaje, suelen fijarse como criterios una serie de objetivos o competencias que el estudiante debe alcanzar.
              br
              br
              |<b>Evaluacin continua.</b> Actividad valorativa que se realiza a lo largo de un proceso, de forma simultnea al desarrollo de los fenmenos evaluados. Permite apreciar la evolucin del objeto evaluado a lo largo del tiempo y valorar el modo en que se va avanzando en el logro de los objetivos propuestos.
              br
              br
              |<b>Evaluacin final.</b> Comprobacin de los resultados logrados al final de un proceso y valoracin de los mismos en funcin de los criterios adoptados.
              br
              br
              |<b>Evaluacin formativa.</b> Evaluacin orientada a facilitar la adopcin de decisiones internas que posibiliten la mejora de los objetos o procesos evaluados.
              br
              br
              |<b>Evaluacin inicial (o evaluacin diagnstica).</b> Descripcin y valoracin del punto de partida en el que se inicia un proceso, permitiendo enfocar y adaptar dicho proceso a la situacin real.
              br
              br
              |<b>Evaluacin para la mejora.</b> Actividad de evaluacin entendida como proceso orientado, ante todo, a la mejora del objeto evaluado. Corresponde a una evaluacin con sentido formativo. 
