                        p Determinar el grado en que se consiguieron las intenciones iniciales del objetivo o del proyecto educativo o de la planificacin y comprobar la aparicin de resultados de aprendizaje no previstos inicialmente, porque en el proceso tambin pueden aparecer resultados no previstos que no estaban contemplados y que son muy valiosos en el proceso de enseanza-aprendizaje.

                      .col-lg-4: img(src='@/assets/curso/24.png', alt='Texto que describa la imagen')
                  .carousel-item.bg-1.p-5.tarjeta
                    .row.align-items-center
                      .col-lg-8
                        p Reforzar el <i>carcter democrtico</i> del proceso de aprendizaje poniendo toda la informacin al servicio de los Aprendices que intervienen directa o indirectamente en el mismo y este aspecto es muy importante en la comunicacin de la evaluacin porque encontramos que hay muchos Instructores que pareciera que se aduean de este proceso, es decir, mantienen oculta las calificaciones mantienen, ocultos los resultados y esto es totalmente negativo por lo tanto es importante que la evaluacin sea del Aprendiz y que l continuamente sepa cules son los resultados o cmo ha ido el proceso. 

                      .col-lg-4: img(src='@/assets/curso/25.png', alt='Texto que describa la imagen')
                  .carousel-item.bg-1.p-5.tarjeta
                    .row.align-items-center
                      .col-lg-8
                        p Diferenciar la calificacin de solo una <i>evaluacin sumativa</i> donde se colocan notas o colocan letras, la evaluacin es mucho ms que la calificacin y debe estar centrada en las dimensiones: describir, interpretar y valorar, el proceso de formacin que ha realizado el Aprendiz, con la mirada puesta siempre en permitir sucesivas correcciones y mejoras.  Por ejemplo: muchas veces tenemos Aprendices que son muy participativos que conocemos que estudian que asisten puntualmente a clases y luego en el examen, ante cualquier situacin nos logran aprobar, en estos casos es donde el Instructor debe colocar en juego los principios de evaluacin que no es lo mismo que la calificacin.

                      .col-lg-4: img(src='@/assets/curso/26.png', alt='Texto que describa la imagen')
              
                
