    #t_1_3.titulo-segundo.color-primario(data-aos='fade-right')
      h2 1.3. Tipos de evaluacin
    p.mb-5 Con respecto al cundo evaluar, se sealan tres momentos de evaluacin: inicial, de proceso y final. Estos momentos coinciden con los tipos de evaluacin: diagnstica, formativa y sumativa o sumaria (Scriven, 1967; Daz Barriga y Hernndez, 2002; Nirenberg, Brawerman y Ruiz, 2003).
    figure.mb-5(data-aos='zoom-in-up')
      img(src='@/assets/curso/27.svg', alt='Texto que describa la imagen')
    h3(data-aos='fade-right') Evaluacin Diagnstica:  
    p.mb-5 Tiene por finalidad identificar las aptitudes, conocimientos, habilidades, destrezas, los intereses y las motivaciones que tienen los aprendices para el logro de los objetivos del proceso formativo que estn por iniciar.  
      br
      br
      |Es importante aclarar que cuando se habla de evaluacin diagnstica no solo se realiza al comienzo del curso, sino que es un proceso que se debe realizar antes de iniciar cada nuevo tema o contenido, es decir, es una evaluacin continua, que permite conocer el grado de motivacin o inters que tienen los aprendices hacia el tema propuesto; descubriendo respuestas como, por ejemplo: 
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-3
        .bloque-texto-b.color-acento-botones.p-4
          .bloque-texto-b__texto
            h2.mb-0 Qu contenidos tienen mayor dificultad?
      .col-lg-7
        p Y esto se puede determinar a partir de la conversacin con los aprendices si ya han visto el tema en tiempos anteriores o si todos los aprendices poseen las mismas habilidades y los conocimientos necesarios para iniciar el proceso de aprendizaje, o si hay estudiantes que tienen ms habilidades o ms conocimientos que otros y eso puede dar la pauta para un futuro trabajo en equipo.
    .row.justify-content-center.mb-5
      .col-lg-10
        .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/28.svg')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0.h5 El Instructor SENA a partir de los resultados de la evaluacin diagnstica adaptar las estrategias que ha planificado para la enseanza o la que va a proponer a los Aprendices para sus intereses y caractersticas de un grupo en concreto.
    h3(data-aos='fade-right') Evaluacin formativa: 
    p.mb-5 Esta evaluacin no tiene la finalidad calificar, sino que tiene la finalidad de determinar en qu medida se estn logrando los resultados de aprendizaje, se aplica durante el desarrollo de la estrategia de aprendizaje y sus resultados permitirn de manera inmediata si fuera el caso reorientar al aprendiz en el proceso; es decir, la evaluacin formativa permite al Instructor el monitoreo continuo para redirigir y verificar el alcance del aprendizaje.  A continuacin, se presentan las formas bsicas de evaluacin formativa:  
    .row.mb-5.justify-content-center
      .col-lg-6
        .tarjeta.tarjetas-text.rounded-0(@mouseover="mostrarIndicador= false")
          .indicador--hover(v-if="mostrarIndicador")
          img(src="@/assets/curso/29.jpg" alt="Adornos flotantes" style="width: 100% ").mb-3
          .tarjetas-text__contenido.px-5.pb-5.pt-3
            h3 Autoevaluacin
            p Como su nombre lo indica es la evaluacin que cada Aprendiz hace de su propio trabajo, es la evaluacin de s mismo, a travs de ella el estudiante puede reconocer sus logros sus deficiencias, analizar y considerar su actuacin individual y cmo se ha integrado al grupo y lo que promueve es el desarrollo de una actitud autocrtica.
      .col-lg-6
        .tarjeta.tarjetas-text.rounded-0(@mouseover="mostrarIndicador= false")
          img(src="@/assets/curso/30.jpg" alt="Adornos flotantes" style="width: 100% ").mb-3
          .tarjetas-text__contenido.px-5.pb-5.pt-3
            h3 Coevaluacin
            p Esta evaluacin es la que tiene que ver con el otro, es decir la evaluacin entre pares o en grupo que es lo que conocemos como evaluacin recproca que realizan los Aprendices que estn siendo evaluados.  El Instructor tradicionalmente no participa del proceso de coevaluacin porque l no est siendo evaluado en ese momento; este proceso se hace sobre la actuacin de cada uno y del grupo como un todo. Puede incluir al Instructor siempre y cuando el Instructor no est calificando.
      p.mb-5.mt-5 Este tipo de evaluacin permite:
      .row.mb-5
        .col-lg-6.mb-2.mb-lg-0
          .d-flex.justify-content-center.align-items-center.h-100.hover
            img(src="@/assets/curso/31.svg" alt="Adornos flotantes" style="width: 85px ").me-3
            p.mb-0 <b>Desarrollar en los Aprendices actitudes y expresar opiniones</b> como resultado de la reflexin y la crtica ante las diferentes situaciones que se estn presentando.  
          hr 
        .col-lg-6.mb-2.mb-lg-0
          .d-flex.justify-content-center.align-items-center.h-100.hover
            img(src="@/assets/curso/32.svg" alt="Adornos flotantes" style="width: 85px ").me-3
            p.mb-0 <b>Aportar soluciones para el mejoramiento individual y colectivo</b>. Poner en prctica la convivencia del grupo, que entre ellos puedan determinar cules son los logros, cules son los avances y determinar por supuesto logros tanto personales como grupales.  
          hr 
      .row.mb-5.align-items-center
        .col-lg-6.mb-2.mb-lg-0
          .d-flex.justify-content-center.align-items-center.h-100.hover
            img(src="@/assets/curso/33.svg" alt="Adornos flotantes" style="width: 85px ").me-3
            p.mb-0 Al <b>Instructor modificar la estrategia de aprendizaje</b>, mejorando los recursos que est utilizando sobre todo cuando se observa que existe un nmero suficiente de Aprendices que no estn logrando el aprendizaje. 
          hr.d-lg-none 
        .col-lg-6.mb-2.mb-lg-0
          .d-flex.justify-content-center.align-items-center.h-100.hover
            img(src="@/assets/curso/34.svg" alt="Adornos flotantes" style="width: 85px ").me-3
            p.mb-0 <b>A los aprendices planificar y desarrollar actividades previas a la evaluacin sumativa tanto individual como grupal</b>, de manera que se puedan resolver esas diferencias antes de verse sometidos a la evaluacin sumativa y adems permite precisar cules son las dificultades de cada Aprendiz.
    .bg--2-claro4.p-3.br-1.mb-5
      .row.justify-content-center
        .col-lg-4.imagen-ab
          img(src="@/assets/curso/35.svg" alt="Adornos flotantes" style="width: 100% ").mb-2
        .col-lg-7
          p En la coevaluacin hacemos un nfasis porque generalmente se presenta un  problema, muchas veces el Instructor califica la estrategia de enseanza o de aprendizaje que est usando y lo hace en nombre de la evaluacin formativa, es  decir, hay una irregularidad si el Instructor disea una estrategia de aprendizaje y la toma como evaluacin, esta no se debera calificar, sino que hace parte del proceso de aprendizaje.
            br
            br
            |Es muy comn ver en nuestros espacios acadmicos que el Instructor propone la elaboracin de un trabajo de investigacin y esa es su estrategia de aprendizaje y la califica o plantea la elaboracin de un mapa conceptual y lo califica, pero esa actividad de evaluacin fue diseada para que el Aprendiz aprenda el contenido.  Por lo tanto, no debe considerarse como evaluacin sumativa.
            br
            br
            |Lo correcto sera que los Aprendices, elaboran un mapa mental para aprender y luego el Instructor si ve la oportunidad de realizar una evaluacin sumativa puede hacerlo con una exposicin de ese mapa, garantizando que el Aprendiz tiene el derecho a equivocarse porque est aprendiendo y no aplicarle evaluacin sumativa en el proceso de aprender.
    p.mb-5 Y haciendo nfasis en esta aclaracin pasamos a la siguiente evaluacin:
    h3(data-aos='fade-right') Evaluacin sumativa
    .row.mb-5
      .col-lg-7(data-aos='fade-right')
        p Tiene por finalidad determinar ahora si el logro de los resultados de aprendizaje, con el fin de calificar al Aprendiz y orientar las decisiones procedentes por parte del Instructor, cuando hablamos de evaluacin sumativa es una actividad especfica para calificar y se cumple a travs de las siguientes formas: parciales, finales, extraordinarias; revisiones, equivalencias o revalidacin es decir es un instrumento especfico que determina una nota, el resultado del proceso de enseanza-aprendizaje.
      .col-lg-5(data-aos='fade-left'): img(src='@/assets/curso/36.jpg')
    #t_1_4.titulo-segundo.color-primario(data-aos='fade-right')
      h2 1.4. Dimensiones de la evaluacin
    .row.mb-5
      .col-lg-7(data-aos='fade-right')
        p Las dimensiones de la evaluacin se expresan como dimensiones del aprendizaje y corresponden a las reas de desarrollo humano que constituyen la integralidad de la formacin profesional, a saber, cognitiva, psicomotriz o procedimental y afectiva o valorativa-actitudinal.
          br
          br
          |Cada competencia debe incluir las tres reas como objetivo general de la formacin; de lo contrario, no podra hablarse de integralidad del proceso formativo. Por otra parte, los resultados de aprendizaje pueden enfocarse en el desempeo de una de las tres reas con el fin de promover logros de objetivos especficos SENA (2013, p. 25-26). En este sentido, conviene recordar que cada tipo de evidencia busca promover el desarrollo de una de las reas mencionadas, as: 
      .col-lg-5(data-aos='fade-left'): img(src='@/assets/curso/37.jpg')
    figure(data-aos='zoom-in-up').mb-5
      img(src='@/assets/curso/38.svg')
    p.mb-5 En consecuencia, con miras a determinar el tipo de evidencia que se solicitar en cada caso y lograr el propsito de la evaluacin, conviene considerar dichas dimensiones:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
      .row(titulo="Aprender a aprender (rea cognitiva)").align-items-center.justify-content-center
        .col-md-2
          figure
            img(src='@/assets/curso/39.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p.mb-0 Corresponde al <b>desarrollo de habilidades de pensamiento de orden superior que facultan al aprendiz para interpretar, para argumentar, para proponer, para discernir, para tomar decisiones, entre otras. El aprendiz sabe el qu, el por qu y el para qu del conocimiento; le encuentra significado a lo aprendido, lo resignifica, lo contextualiza y lo aplica, trascendiendo procesos de memorizacin que, si bien son importantes, son insuficientes</b> (2013:36). Responde a la pregunta qu debe saber el aprendiz para lograr determinado resultado de aprendizaje?
      .row(titulo="Aprender a hacer (rea psicomotriz, procedimental o biofsica)").align-items-center.justify-content-center
        .col-md-2
          figure
            img(src='@/assets/curso/40.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p.mb-0 <b>En esta rea converge la aplicacin de los principios cientficos y el uso de tecnologas y tcnicas a la produccin y distribucin de bienes y servicios, es decir, para el contexto productivo; igualmente para el contexto social</b> (2013:30). Responde a la pregunta qu debe saber hacer el aprendiz para lograr determinado resultado de aprendizaje?
      .row(titulo="Aprender a ser (afectiva o valorativa-actitudinal)").align-items-center.justify-content-center
        .col-md-2
          figure
            img(src='@/assets/curso/41.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p.mb-0 Todo ser humano <b>es</b> y se <b>construye humanamente</b> en relacin con los otros seres con quienes interacta en medio de un entorno, esta interaccin se expresa mediante acciones, actitudes, lenguajes, resultado de un proceso de aprendizaje que le permite al aprendiz reconfigurar desde s mismo a partir del fortalecimiento de un sistema de valores y actitudes que le posibilita crecer como persona, como ciudadano y como trabajador mediante el establecimiento de interrelaciones asertivas que redundan en la construccin de comunidad, de ciudadana y de productividad (2013:36-37). Responde a la pregunta cmo debe ser el aprendiz para lograr determinado resultado(s) de aprendizaje?
    #t_1_5.titulo-segundo.color-primario(data-aos='fade-right')
      h2 1.5 Mtodos de evaluacin 
    p.mb-5 La visin actual de la evaluacin no solo est orientada a su carcter medicin lista, es decir a realizar exmenes escritos y calificados mediante puntuaciones cuantitativas, adems las pruebas estandarizadas ignoran la importancia del conocimiento holstico y la integracin de todo el conocimiento; en este sentido el Instructor del siglo XXI se encuentra frente a un reto importante que es:
    .row.justify-content-center.mb-5
      .col-lg-10
        .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/42.svg')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0.h5 Desarrollar Mtodos de evaluacin que respondan a una interpretacin del conocimiento y a una transferencia de dicho conocimiento a otros contextos.
    p.mb-5 Los mtodos de evaluacin alternativos a las necesidades de los ciudadanos de la era del conocimiento estn enfocados ms hacia el proceso que el resultado, concentrndose en el crecimiento del Aprendiz, donde se destacan las fortalezas de los Aprendices en lugar de las debilidades las variedades de sus opciones permiten tomar en consideracin los estilos de aprendizaje las capacidades lingsticas y las experiencias culturales y educativas.
      br
      br
      |A continuacin, se abordan algunos mtodos donde las tcnicas asociadas son la observacin, la entrevista y el anlisis o produccin de documentos (Ibarra & Rodrguez,, 1999).
    TabsA.color-primario.mb-5
      .tarjeta.bg--2-claro5.p-4(titulo="Observacin")
        h4 Observacin
        p A travs de la observacin se puede obtener informacin sobre un hecho (producto) y sobre cmo se produce (proceso).
        h4 Se debe contemplar:
        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta.bg--2
              span 1
            | El objeto de la observacin (qu vamos a observar?).
          li 
            .lista-ol--cuadro__vineta.bg--2
              span 2
            | El contexto y la naturaleza en el que se desarrolla la situacin que observamos.
          li 
            .lista-ol--cuadro__vineta.bg--2
              span 3
            | A cuntos sujetos vamos a observar.
          li 
            .lista-ol--cuadro__vineta.bg--2
              span 4
            | La duracin total y la distribucin del tiempo de observacin.
          li 
            .lista-ol--cuadro__vineta.bg--2
              span 5
            | Cmo vamos a registrar la informacin.
          li 
            .lista-ol--cuadro__vineta.bg--2
              span 6
            | La representatividad de las observaciones.
          li 
            .lista-ol--cuadro__vineta.bg--2
              span 7
            | El valor de los comportamientos observados.
      .tarjeta.bg--2-claro5.p-4(titulo="Entrevista")
        h4 Entrevista
        p La entrevista es una tcnica de recogida de informacin mediante la cual la persona que entrevista solicita y obtiene informacin de otra persona o grupo de personas. Se basa en una relacin personal y una interaccin verbal.
          br
          br
          |En evaluacin, la entrevista en profundidad ofrece amplias posibilidades, ya que nos permite conocer aspectos sobre el proceso de aprendizaje (estrategias desarrolladas, planificacin de tareas, dificultades, medios utilizados), y no solo sobre el producto.
      .tarjeta.bg--2-claro5.p-4(titulo="Anlisis de produccin o documentos")
        h4 Anlisis de produccin o documentos
        p Los documentos de aprendizaje hacen referencia a todo tipo de material escrito producido por los estudiantes (portafolios, ensayos, recensiones, proyectos de investigacin, memorias, exmenes, etc.), cuyo anlisis nos puede proporcionar informacin sobre estos, sobre el proceso de enseanza-aprendizaje y/o sobre su resultado. 
    #t_1_6.titulo-segundo.color-primario(data-aos='fade-right')
      h2 1.6 Estrategias de evaluacin
    p.mb-5 Es un plan de accin para valorar el aprendizaje de los aprendices, reconocer sus avances e identificar las interferencias, a fin de realizar una intervencin efectiva en los procesos de enseanza-aprendizaje.  Entonces las tcnicas de evaluacin son procedimientos utilizados por el Instructor para obtener informacin relacionada a los procesos de enseanza-aprendizaje y los instrumentos de evaluacin son recursos que se utilizan para recolectar y registrar informacin de los aprendices y su prctica docente.
    .row.justify-content-center.mb-5
      .col-lg-10
        .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/43.jpg')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0.h5 La RAE, define <b>"Medio"</b> como <b>"cosa que puede servir para un determinado fin"</b>. En este caso, el fin es evaluar, por tanto, <b>Qu se entiende por medio de evaluacin?</b> Pruebas o evidencias que sirven para recabar informacin sobre el objeto a evaluar.
              br
              br
              |<b>Por ejemplo:</b> El ensayo, la prueba objetiva, el examen, el portafolio, o el mapa conceptual, la infografa, entre otros.
    h3(data-aos='fade-right') Tcnicas de evaluacin
    p.mb-5 Son respuestas al "cmo hacer'' para verificar el alcance de un fin o resultado propuesto. Se sitan a nivel de los hechos o de las etapas prcticas que, a modo de dispositivos auxiliares, permiten la aplicacin del mtodo. Tienen un carcter prctico y operativo (Procedimiento para la ejecucin de acciones de formacin profesional. SENA. 2006. P.25). Veamos algunos de ellos, expuestos en el documento recin referenciado (p. 49-50). Una tcnica sirve para recoger sistemticamente informacin.
    SlyderB.mb-5(:datos="datosSlyder")
    p.mb-5 Otras tcnicas comnmente empleadas son:
    .row.justify-content-center
      .col-lg-4.mb-3
        .tarjeta.bg--1-claro.p-5.position-relative.d-flex.align-items-center.hover
          .cont-img(style="position: absolute; top:0 ; left: -2%; height: 100%;").d-flex.justify-content-center.align-items-center
            img(src='@/assets/curso/51.svg' style="width: 106px;").m-auto
          p.ms-5.ps-3.mb-0.h5 La mesa redonda
      .col-lg-4.mb-3
        .tarjeta.bg--1-claro.p-5.position-relative.d-flex.align-items-center.hover
          .cont-img(style="position: absolute; top:0 ; left: -2%; height: 100%;").d-flex.justify-content-center.align-items-center
            img(src='@/assets/curso/52.svg' style="width: 106px;").m-auto
          p.ms-5.ps-3.mb-0.h5 El conversatorio
      .col-lg-4.mb-3
        .tarjeta.bg--1-claro.p-5.position-relative.d-flex.align-items-center.hover
          .cont-img(style="position: absolute; top:0 ; left: -2%; height: 100%;").d-flex.justify-content-center.align-items-center
            img(src='@/assets/curso/53.svg' style="width: 106px;").m-auto
          p.ms-5.ps-3.mb-0.h5 La entrevista
    .row.justify-content-center
      .col-lg-4.mb-3
        .tarjeta.bg--1-claro.p-5.position-relative.h-100.d-flex.align-items-center.hover
          .cont-img(style="position: absolute; top:0 ; left: -2%; height: 100%;").d-flex.justify-content-center.align-items-center
            img(src='@/assets/curso/54.svg' style="width: 106px;").m-auto
          p.ms-5.ps-3.mb-0.h5 Pruebas de comprensin, anlisis, discusin crtica y, en general, de apropiacin de conceptos
      .col-lg-4.mb-3
        .tarjeta.bg--1-claro.p-5.position-relative.h-100.d-flex.align-items-center.hover
          .cont-img(style="position: absolute; top:0 ; left: -2%; height: 100%;").d-flex.justify-content-center.align-items-center
            img(src='@/assets/curso/55.svg' style="width: 106px;").m-auto
          p.ms-5.ps-3.mb-0.h5 Foro, panel y seminario
      .col-lg-4.mb-3
        .tarjeta.bg--1-claro.p-5.position-relative.h-100.d-flex.align-items-center.hover
          .cont-img(style="position: absolute; top:0 ; left: -2%; height: 100%;").d-flex.justify-content-center.align-items-center
            img(src='@/assets/curso/56.svg' style="width: 106px;").m-auto
          p.ms-5.ps-3.mb-0.h5 Taller
    h3.mt-5(data-aos='fade-right') Instrumentos de evaluacin
    p.mb-5 Son herramientas empleadas por el instructor con el fin de recolectar evidencias de aprendizaje, ya sea de conocimientos, desempeo y producto, en las distintas etapas del proceso formativo en concordancia con las actividades de aprendizaje expuestas en las guas de aprendizaje. A continuacin, se citan algunos instrumentos de evaluacin expuestos en el documento institucional Gua para elaborar instrumentos de evaluacin en mdulos de formacin por competencia laboral (SENA. 2003:16-19).
    .row.mb-5.justify-content-center
      .col-lg-7(data-aos='fade-right')
        h4 Lista de chequeo o verificacin: 
        p Est conformada por el enunciado afirmativo o interrogativo alrededor de una evidencia de aprendizaje en la cual se describen los aspectos ms relevantes a tener en cuenta o verificar en el proceso de formacin. Permite de esta manera evaluar si el estudiante cumple o no con la competencia en la actividad de enseanza aprendizaje realizada. Debe indicar el nmero de veces que se considere necesario realizar dicho anlisis de acuerdo con lo previsto en las evidencias aprendizaje requeridas por el mdulo.
